/* src/App.css */

/* Base styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f9;
  color: #333;
}

.wrapper2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.wrapper {
  width: 90%;
  max-width: 1200px;
  margin: 20px 0;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.Dropdown {
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px;
}

.chart-wrapper {
  width: 100%;
  margin: 20px 0;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h3 {
  text-align: center;
  color: #555;
  margin-bottom: 20px;
}

.Select__control {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  background-color: #fff;
}

.Select__menu {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 4px;
}

.Select__option--is-focused {
  background-color: #f4f4f4;
}

/* Additional aesthetic improvements */
.wrapper, .Dropdown, .chart-wrapper {
  transition: all 0.3s ease;
}

.wrapper:hover, .Dropdown:hover, .chart-wrapper:hover {
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

h3 {
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 600;
}

.Select__control, .Select__menu {
  font-family: 'Helvetica Neue', sans-serif;
}

/* Add responsive design */
@media (max-width: 768px) {
  .wrapper, .chart-wrapper {
    padding: 15px;
  }
  
  .Dropdown {
    width: 80%;
  }
}

@media (max-width: 480px) {
  .wrapper, .chart-wrapper {
    padding: 10px;
  }
  
  .Dropdown {
    width: 100%;
  }
}
